.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 600px;
  max-width: 100%;
  /* max-height: calc(100vh - 88px); */
  box-sizing: border-box;
  text-align: start;
  padding: 100px 0 0 16px;
}

.constructorContainer {
}

.constructorContainer,
.constructorList {
  align-items: stretch;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 16px;
  list-style: none;
}

.constructorList {
  max-height: 464px;
  overflow-y: scroll;
  scrollbar-width: thin;
  scrollbar-color: #8585ad #2f2f37;
  margin-bottom: 0;
  padding: 0;
}

.constructorList::-webkit-scrollbar {
  width: 8px;
}

.constructorList::-webkit-scrollbar-track {
  background: #2f2f37;
}

.constructorList::-webkit-scrollbar-thumb {
  background-color: #8585ad;
  border: 3px solid #8585ad;
}

.containerOrder {
  margin: 40px 0 0;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0px;
  gap: 40px;
}

.containerPrice {
  display: flex;
  align-items: center;
}

.icon svg {
  width: 36px;
  height: 36px;
}
